<template>
	<div class="mine">
		<div class="box">
			<p class="Icon" @click="back"><i class="fa fa-arrow-left"></i></p>
			<p class="title">猫眼电影</p>
		</div>
		<div class="Login_page">
			<div class="login_input">
				<p><input type="text" placeholder="账户名/手机号/Email" class="inp" @blur="fun()" v-model="username"/></p>
				<p><input type="password" placeholder="请输入你的密码" class="inp" v-model="passworld"/></p>
			</div>
			<div class="Login_button">
				<p><button @click="Login">登录</button></p>
			</div>
		</div>
		<div class="register">
			<span @click="register">立即注册</span>
			<span @click="goback">找回密码</span>		
		</div>
	</div>
</template>

<script>
	import { setCookie, getCookie } from "../../assets/cookie.js";
	import { Toast } from 'mint-ui';
export default {
		beforeRouteEnter(to,from,next){
		next(vm=>{
			console.log(vm)
			if(getCookie("username")){
				vm.$router.push("/mylogin")
			}else{
				next()
			}
		})
	},
	created() {
		
	},
	data() {
		return {
			username: "",
			passworld: ""
		}
	},
	methods: {
		back() {
			this.$router.push("/index")
		},
		fun() {
			//console.log(this.username)
			var reg = /^1(?:3\d|4[4-9]|5[0-35-9]|6[67]|7[013-8]|8\d|9\d)\d{8}$|^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
			if(!reg.test(this.username)) {
				alert("请输入正确的用户名");
				this.username = ""
			}
		},
		Login() {
			if(this.username === "") {
				alert("请输入密码");
				return
			}
			this.$http.get("http://localhost:3000/list?username=" + this.username).then(res => {
				if(!res.data.length == 0) {
					this.$http.get("http://localhost:3000/list?username=" + this.username + "&passworld=" + this.passworld).then(data => {
						if(data.data.length === 0) {
							alert("密码有误")
						} else {
							setCookie('username', this.username);
							Toast({
							  message: '正在跳转',
							  position: 'center',
							  duration: 200
							});
							this.$router.push("/index")
						}
					})
				} else {
					alert("用户不存在")
				}

			})

		},
		register() {
			this.$router.push("/register")
		},
		goback() {
			this.$router.push("/goback")
		}
	}
}</script>

<style scoped>.mine {
	width: 100%;
	height: 60px;
}

.mine .box {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between;
	background: #df2d2d;
	color: #fff;
	font-size: 20px;
	text-align: center;
	line-height: 60px;
}

.mine .Icon {
	width: 20%;
	height: 60px;
}

.mine .title {
	width: 80%;
	height: 100%;
}

.mine .Login_page {
	width: 100%;
	height: 125px;
	margin-top: 60px;
}

.mine .Login_page .login_input {
	width: 100%;
	height: 80px;
}

.mine .Login_page .login_input p input {
	width: 100%;
	height: 40px;
	border: none;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	font-size: 18px;
	padding: 10px;
}

.mine .Login_page .Login_button {
	width: 100%;
	height: 30px;
}

.mine .Login_page .Login_button button {
	width: 100%;
	height: 45px;
	font-size: 24px;
	line-height: 24px;
	background: #DF2D2D;
	color: #fff;
	font-family: "微软雅黑";
}

.inp:focus {
	outline: none;
	border: 1px solid #CCCCCC;
	background: #f0ecec;
}


/*注册*/

.mine .register {
	width: 100%;
	color: #FF0000;
	height: 60px;
	line-height: 50px;
	display: flex;
	justify-content: space-between;
}

.mine .register span {
	width: 100px;
	height: 100%;
	display: block;
	text-align: center;
}</style>


